































































































































import EventModal from "@/components/events/EventModal.vue";
import {initialSm} from "@/core/constants/api/initialSm";
import {GlobalTag, SequenceManager} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import {Getter} from "vuex-class";
import AsModal from "../../mixins/AsModal";
import {initialSequenceJSON, initialTask} from "@/core/constants/initialSequenceJSON";
import {SequenceEvent, SequenceJSON} from "@/types/SequenceJSON";
@Component({
  components: {EventModal},
})
export default class EditSequenceModal extends Mixins(AsModal) {
  @Getter("permission/status") readonly status!: number;
  @Prop(Number) readonly id!: number;

  @Emit("refresh") refresh(): void {}

  displayEventModal: boolean = false;
  selectedTaskId: number = 0;
  newEventId: number = 0;
  formData: SequenceManager = {...initialSm()};
  tags: number[] = [];
  tagName: string = "";
  sequenceJSON: SequenceJSON = initialSequenceJSON();
  tagList: GlobalTag[] = [];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.api.getTagList(null).then((res) => {
        this.tagList = res;
      });
    }
    this.api.getSm(this.id).then((res) => {
      this.formData = res;
      this.sequenceJSON = res.content ? JSON.parse(res.content) : initialSequenceJSON();
      this.tags = this.formData.tags.split(",").map((tag) => {
        return Number(tag);
      });
    });
  }

  addTag(): void {
    this.api.addTag({name: this.tagName}).then((res) => {
      this.api.getTagList(null).then((tagList) => {
        this.tagList = tagList;
        this.tags = [...this.tags, res.id];
        this.tagName = null;
      });
    });
  }

  submit(): void {
    this.formData.tags = this.tags.join(",");
    this.sequenceJSON.name = this.formData.sequence_name;
    this.formData.content = JSON.stringify(this.sequenceJSON);

    this.api.editSm(this.formData, this.formData.id).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.formData = {...initialSm()};
    this.sequenceJSON = initialSequenceJSON();
    this.tags = [];
    this.tagName = "";
    this.dismiss();
  }

  addTask(): void {
    this.$prompt("Task Name", "New Task", {
      confirmButtonText: "Add",
      cancelButtonText: "Cancel",
    })
      .then((action: any) => {
        const id = this.sequenceJSON.tasks.length + 1;
        const task = initialTask(action.value, id);
        this.sequenceJSON.tasks.push(task);
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "Cancel!",
        });
      });
  }

  addEvent(taskId: any): void {
    this.newEventId = this.sequenceJSON.tasks[taskId].events.length + 1;
    this.displayEventModal = true;
    this.selectedTaskId = taskId;
  }

  getEvent(event: SequenceEvent): void {
    this.sequenceJSON.tasks[this.selectedTaskId].events.push(event);
  }
}
