  deleteResultData(index: number): void {
    this.eventData.resultData.splice(index, 1);
  }

  deleteRequirementData(index: number): void {
    this.eventData.requirementData.splice(index, 1);
  }

  addResultData(data: object) {
    if (data) {
      this.eventData.resultData.push({...data} as any);
    }
  }

  addRequirementData(data: object) {
    if (data) {
      this.eventData.requirementData.push({...data} as any);
    }
  }

  get analysisNameFilterByCategory(): any[] {
    return this.analysisName.filter((option) => {
              return option.category.some( (category) => {
                return category==this.eventFields[0].value
              });
            });
  }

  getEventInfo(title:string): void {
    switch(title) {
      case "Level vs Time": {
        this.eventInfo = this.mockEventInfo[0];
        break;
      }
      case "Fourier Spectrum": {
        this.eventInfo = this.mockEventInfo[1];
        break;
      }
      case "Octave Spectrum": {
        this.eventInfo = this.mockEventInfo[2];
        break;
      }
      default: {
        this.eventInfo = {};
        break;
      }
    }
  }

