













































import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class AddPlaybackChannelModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(data: object): void {}

  clean(): void {
    this.dismiss();
  }

  resetForm(): void {
    this.$nextTick(()=> {
      const ref: any = this.$refs["playbackChannelRuleFormRef"];
      ref.resetFields();
    });
    this.playbackChannelRuleForm = {
      adjustment: -10,
      channelIndex: 1,
      name: "",
      sourceFileChannel: 1,
      sourceFileName: "",
      sourceFileLength: 15,
      delayTime: 3,
      skipTime: 5,
      takeLength: 10,
    };
  }

  addPlayback() : void {
    const ref: any = this.$refs["playbackChannelRuleFormRef"];
    ref.validate((valid) => {
      if (valid) {
        this.refresh(this.playbackChannelRuleForm);
        this.clean()
      } else {
        return false;
      }
    });
  }

  playbackChannelRuleForm = {
    adjustment: 0,
    channelIndex: 1,
    name: "",
    sourceFileChannel: 1,
    sourceFileName: "",
    sourceFileLength: 15,
    delayTime: 3,
    skipTime: 5,
    takeLength: 10,
  };
  playbackChannelRule = {
    name: [
      { required: true, message: 'Channel name is required.', trigger: 'change' }
    ],
  };
}
