






















import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class AddCalculationResultModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(data: object): void {}

  clean(): void {
    this.dismiss();
  }

  resetForm(): void {
    this.$nextTick(()=> {
      const ref: any = this.$refs["resultRuleFormRef"];
      ref.resetFields();
    });
    this.resultRuleForm = {
      name: "",
      resource: ""
    };
  }

  addResult() : void {
    const ref: any = this.$refs["resultRuleFormRef"];
    ref.validate((valid) => {
      if (valid) {
        this.refresh(this.resultRuleForm);
        this.clean()
      } else {
        return false;
      }
    });
  }

  resultRuleForm = {
    name: "",
    resource: ""
  };
  resultRule = {
    name: [
      { required: true, message: 'Channel name is required.', trigger: 'change' }
    ],
  };
}
