


























































import AddPlaybackChannelModal from "@/components/events/AddPlaybackChannelModal.vue";
import AddRecordingChannelModal from "@/components/events/AddRecordingChannelModal.vue";
import UploadAudioModal from "@/components/events/UploadAudioModal.vue";
import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins} from "vue-property-decorator";
@Component({
  components: {AddRecordingChannelModal, AddPlaybackChannelModal, UploadAudioModal},
})
export default class EventMeasureModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  displayUploadAudioModal: boolean = false;
  displayAddPlaybackChannelModal: boolean = false;
  displayAddRecordingChannelModal: boolean = false;

  eventData = {
    eventLength: 0,
    audio: "",
    playbackChannelData: [{name: "Speaker Front", adjustment: -10}],
    recordingData: [{name: "Speaker Left"}, {name: "Speaker Right"}],
  };

  clean(): void {
    this.dismiss();
  }

  deletePlaybackData(index: number): void {
    this.eventData.playbackChannelData.splice(index, 1);
  }

  deleteRecordingData(index: number): void {
    this.eventData.recordingData.splice(index, 1);
  }

  addPlaybackData(data: object) {
    if (data) {
      this.eventData.playbackChannelData.push({...data} as any);
    }
  }

  addRecordingData(data: object) {
    if (data) {
      this.eventData.recordingData.push({...data} as any);
    }
  }
}
