


























import AsModal from "@/mixins/AsModal";
import {ChangePassword} from "@/types/ApiTypes";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class ChangePasswordModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  formData: ChangePassword = {password: "", confirm_password: ""};
  clean(): void {
    this.formData = {password: "", confirm_password: ""};
    this.dismiss();
  }

  change(): void {
    this.api.changePassword(this.formData).then(() => this.clean());
  }
}
