





















import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class EventPauseModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  eventData = {
    pauseMode: "Countdown",
    eventLength: 5
  };
  eventFields: object[] = [
    {
      title: "Pause Mode",
      type: "select",
      options: [
        {
          title: "Countdown",
        },
        {
          title: "Manual Stop",
        }
      ],
      placeholder: ""
    },
    {
      title: "Event Length [s]",
      type: "input"
    }
  ]
}
